import getters from './getters'
const state={
	cart:'',//
	cartNone:'',//
	count:1,//
	showShop:true,//
	showCom:false,//
	details:false,//
	hideTab:true,//tab
};

const mutations={
	hideCart(state){
		state.cart=false;
		state.cartNone=true;
	},
	showCart(state){
		state.cart=true;
		state.cartNone=false;
	},
	increment(state){
		state.count++;			
	},
	decrement(state){
		if(state.count>1){
			state.count--;	
		}		
	},
	check(state){
		if(state.count<0){
			state.count=1;
		}	
	},
	tabShowLeft(state){
		state.showShop=true;
		state.showCom=false;
	},
	tabShowRight(state){
		state.showShop=false;
		state.showCom=true;
	},
	showDetails(state){
		state.details=true;
	},
	hideDetails(state){
		state.details=false;
	},
	hideTable(state){
		state.hideTab=false;
	},
	showTable(state){
		state.hideTab=true;
	}
}
export default{
	state,
	getters,
	mutations
}


