export default {
	hideCart:({commit}) => {//
		commit('hideCart');
	},
	showCart: ({commit}) => {//
		commit('showCart');
	},
	increment:({commit})=>{//
		commit('increment');
	},
	decrement:({commit})=>{//
		commit('decrement');
	},
	check:({commit})=>{//0
		commit('check');
	},
	tabShowLeft:({commit})=>{//
		commit('tabShowLeft');
	},
	tabShowRight:({commit})=>{//
		commit('tabShowRight');
	},
	showDetails:({commit})=>{//
		commit('showDetails');
	},
	hideTable:({commit})=>{//
		commit('hideTable');
	},
	showTable:({state,commit})=>{//
		//state.mutations.hideTab=true;
		commit('showTable');
	}
}


