export default {
	cart: (state) => {
		return state.cart;//
	},
	cartNone: (state) => {
		return state.cartNone;//
	},
	count:(state)=>{
		return state.count;//
	},
	showShop:(state)=>{
		return state.showShop;//
	},
	showCom:(state)=>{
		return state.showCom;//
	},
	details:(state)=>{
		return state.details;//
	},
	hideTab:(state)=>{//
		return state.hideTab;
	}

}


